<template>
  <div class="export-view">
    <el-row :gutter="20">
      <!-- 导出配置 -->
      <el-col :xs="24" :lg="16">
        <el-card title="数据导出">
          <el-form
            ref="exportFormRef"
            :model="exportForm"
            :rules="exportRules"
            label-width="120px"
          >
            <!-- 导出格式 -->
            <el-form-item label="导出格式" prop="format">
              <el-radio-group v-model="exportForm.format">
                <el-radio-button label="excel">Excel</el-radio-button>
                <el-radio-button label="pdf">PDF</el-radio-button>
                <el-radio-button label="csv">CSV</el-radio-button>
                <el-radio-button label="json">JSON</el-radio-button>
              </el-radio-group>
            </el-form-item>

            <!-- 数据范围 -->
            <el-form-item label="数据范围" prop="range">
              <el-radio-group v-model="exportForm.range" @change="handleRangeChange">
                <el-radio label="all">全部数据</el-radio>
                <el-radio label="filtered">筛选数据</el-radio>
                <el-radio label="selected">选中数据</el-radio>
              </el-radio-group>
            </el-form-item>

            <!-- 筛选条件 -->
            <div v-if="exportForm.range === 'filtered'" class="filter-section">
              <el-form-item label="记录类型">
                <el-select
                  v-model="exportForm.filters.type"
                  placeholder="选择记录类型"
                  clearable
                  multiple
                >
                  <el-option
                    v-for="type in recordTypes"
                    :key="type.id"
                    :label="type.name"
                    :value="type.name"
                  />
                </el-select>
              </el-form-item>

              <el-form-item label="状态">
                <el-select
                  v-model="exportForm.filters.status"
                  placeholder="选择状态"
                  clearable
                  multiple
                >
                  <el-option label="草稿" value="draft" />
                  <el-option label="已发布" value="published" />
                  <el-option label="已归档" value="archived" />
                </el-select>
              </el-form-item>

              <el-form-item label="创建时间">
                <el-date-picker
                  v-model="exportForm.filters.dateRange"
                  type="daterange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  format="YYYY-MM-DD"
                  value-format="YYYY-MM-DD"
                />
              </el-form-item>

              <el-form-item label="创建者">
                <el-select
                  v-model="exportForm.filters.createdBy"
                  placeholder="选择创建者"
                  clearable
                  multiple
                  filterable
                >
                  <el-option
                    v-for="user in users"
                    :key="user.id"
                    :label="user.username"
                    :value="user.id"
                  />
                </el-select>
              </el-form-item>
            </div>

            <!-- 选中数据 -->
            <div v-if="exportForm.range === 'selected'" class="selected-section">
              <el-form-item label="选中记录">
                <div class="selected-records">
                  <div v-if="selectedRecords.length === 0" class="empty-selection">
                    <el-empty description="请先选择要导出的记录" />
                    <el-button type="primary" @click="showRecordSelector = true">
                      选择记录
                    </el-button>
                  </div>
                  <div v-else>
                    <div class="selected-count">
                      已选择 {{ selectedRecords.length }} 条记录
                    </div>
                    <div class="selected-list">
                      <el-tag
                        v-for="record in selectedRecords.slice(0, 10)"
                        :key="record.id"
                        closable
                        @close="removeSelectedRecord(record)"
                      >
                        {{ record.title }}
                      </el-tag>
                      <span v-if="selectedRecords.length > 10" class="more-count">
                        等 {{ selectedRecords.length }} 条记录
                      </span>
                    </div>
                    <el-button size="small" @click="showRecordSelector = true">
                      重新选择
                    </el-button>
                    <el-button size="small" type="danger" @click="clearSelectedRecords">
                      清空选择
                    </el-button>
                  </div>
                </div>
              </el-form-item>
            </div>

            <!-- 导出字段 -->
            <el-form-item label="导出字段">
              <div class="field-selection">
                <el-checkbox
                  v-model="selectAllFields"
                  :indeterminate="isIndeterminate"
                  @change="handleSelectAllFields"
                >
                  全选
                </el-checkbox>
                
                <el-checkbox-group v-model="exportForm.fields" @change="handleFieldChange">
                  <el-checkbox label="id">ID</el-checkbox>
                  <el-checkbox label="title">标题</el-checkbox>
                  <el-checkbox label="type">类型</el-checkbox>
                  <el-checkbox label="status">状态</el-checkbox>
                  <el-checkbox label="content">内容</el-checkbox>
                  <el-checkbox label="creator">创建者</el-checkbox>
                  <el-checkbox label="createdAt">创建时间</el-checkbox>
                  <el-checkbox label="updatedAt">更新时间</el-checkbox>
                </el-checkbox-group>
              </div>
            </el-form-item>

            <!-- 导出模板 -->
            <el-form-item label="导出模板">
              <div class="template-section">
                <el-select
                  v-model="exportForm.templateId"
                  placeholder="选择导出模板（可选）"
                  clearable
                >
                  <el-option
                    v-for="template in exportTemplates"
                    :key="template.id"
                    :label="template.name"
                    :value="template.id"
                  />
                </el-select>
                
                <el-button @click="showTemplateDialog = true">
                  管理模板
                </el-button>
              </div>
            </el-form-item>

            <!-- 操作按钮 -->
            <el-form-item>
              <el-button
                type="primary"
                :loading="exporting"
                @click="handleExport"
              >
                <el-icon><Download /></el-icon>
                开始导出
              </el-button>
              
              <el-button @click="handlePreview">
                <el-icon><View /></el-icon>
                预览数据
              </el-button>
              
              <el-button @click="handleReset">
                <el-icon><Refresh /></el-icon>
                重置
              </el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>

      <!-- 导出历史 -->
      <el-col :xs="24" :lg="8">
        <el-card title="导出历史">
          <div class="export-history">
            <div v-if="exportHistory.length === 0" class="empty-history">
              <el-empty description="暂无导出记录" />
            </div>
            <div v-else>
              <div
                v-for="task in exportHistory"
                :key="task.id"
                class="history-item"
              >
                <div class="history-header">
                  <span class="history-format">{{ task.format.toUpperCase() }}</span>
                  <el-tag
                    :type="getTaskStatusType(task.status)"
                    size="small"
                  >
                    {{ getTaskStatusText(task.status) }}
                  </el-tag>
                </div>
                
                <div class="history-progress" v-if="task.status === 'processing'">
                  <el-progress :percentage="task.progress" />
                </div>
                
                <div class="history-info">
                  <p class="history-time">{{ formatTime(task.createdAt) }}</p>
                  <p v-if="task.error" class="history-error">{{ task.error }}</p>
                </div>
                
                <div class="history-actions">
                  <el-button
                    v-if="task.status === 'completed' && task.fileUrl"
                    size="small"
                    type="primary"
                    @click="downloadExportFile(task)"
                  >
                    下载
                  </el-button>
                  
                  <el-button
                    v-if="task.status === 'processing'"
                    size="small"
                    type="warning"
                    @click="cancelExportTask(task)"
                  >
                    取消
                  </el-button>
                </div>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!-- 记录选择对话框 -->
    <el-dialog
      v-model="showRecordSelector"
      title="选择记录"
      width="80%"
      top="5vh"
    >
      <RecordSelector
        v-model="selectedRecords"
        @confirm="showRecordSelector = false"
        @cancel="showRecordSelector = false"
      />
    </el-dialog>

    <!-- 模板管理对话框 -->
    <el-dialog
      v-model="showTemplateDialog"
      title="导出模板管理"
      width="70%"
    >
      <TemplateManager
        @template-updated="loadExportTemplates"
        @close="showTemplateDialog = false"
      />
    </el-dialog>

    <!-- 数据预览对话框 -->
    <el-dialog
      v-model="showPreviewDialog"
      title="数据预览"
      width="90%"
      top="5vh"
    >
      <div v-if="previewData" class="preview-content">
        <div class="preview-info">
          <p>预计导出 {{ previewData.total }} 条记录</p>
        </div>
        
        <el-table :data="previewData.items" stripe max-height="400">
          <el-table-column
            v-for="field in exportForm.fields"
            :key="field"
            :prop="field"
            :label="getFieldLabel(field)"
            show-overflow-tooltip
          />
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed, onMounted, watch } from 'vue'
import { ElMessage, ElMessageBox, type FormInstance, type FormRules } from 'element-plus'
import { useAuthStore } from '@/stores/auth'
import { http } from '@/utils/request'
import { API_ENDPOINTS } from '@/config/api'
import type { Record, RecordType, User, ExportTemplate, ExportTask } from '@/types'
import dayjs from 'dayjs'

const authStore = useAuthStore()

// 响应式数据
const exportFormRef = ref<FormInstance>()
const exporting = ref(false)
const showRecordSelector = ref(false)
const showTemplateDialog = ref(false)
const showPreviewDialog = ref(false)

const recordTypes = ref<RecordType[]>([])
const users = ref<User[]>([])
const exportTemplates = ref<ExportTemplate[]>([])
const exportHistory = ref<ExportTask[]>([])
const selectedRecords = ref<Record[]>([])
const previewData = ref<any>(null)

// 表单数据
const exportForm = reactive({
  format: 'excel',
  range: 'all',
  filters: {
    type: [] as string[],
    status: [] as string[],
    dateRange: null as [string, string] | null,
    createdBy: [] as number[]
  },
  fields: ['title', 'type', 'status', 'creator', 'createdAt'],
  templateId: null as number | null
})

// 字段选择状态
const selectAllFields = ref(false)
const isIndeterminate = ref(true)

// 表单验证规则
const exportRules: FormRules = {
  format: [
    { required: true, message: '请选择导出格式', trigger: 'change' }
  ],
  range: [
    { required: true, message: '请选择数据范围', trigger: 'change' }
  ]
}

// 计算属性
const allFields = ['id', 'title', 'type', 'status', 'content', 'creator', 'createdAt', 'updatedAt']

// 方法
const formatTime = (time: string) => {
  return dayjs(time).format('YYYY-MM-DD HH:mm:ss')
}

const getTaskStatusType = (status: string) => {
  const statusMap: Record<string, string> = {
    pending: 'info',
    processing: 'warning',
    completed: 'success',
    failed: 'danger'
  }
  return statusMap[status] || 'info'
}

const getTaskStatusText = (status: string) => {
  const statusMap: Record<string, string> = {
    pending: '等待中',
    processing: '处理中',
    completed: '已完成',
    failed: '失败'
  }
  return statusMap[status] || status
}

const getFieldLabel = (field: string) => {
  const fieldMap: Record<string, string> = {
    id: 'ID',
    title: '标题',
    type: '类型',
    status: '状态',
    content: '内容',
    creator: '创建者',
    createdAt: '创建时间',
    updatedAt: '更新时间'
  }
  return fieldMap[field] || field
}

// 加载数据
const loadRecordTypes = async () => {
  try {
    const response = await http.get<RecordType[]>(API_ENDPOINTS.RECORD_TYPES.LIST)
    recordTypes.value = response
  } catch (error) {
    console.error('Failed to load record types:', error)
  }
}

const loadUsers = async () => {
  try {
    // 这里应该有一个获取用户列表的API
    // const response = await http.get<User[]>('/users')
    // users.value = response
    
    // 模拟数据
    users.value = [
      { id: 1, username: 'admin', email: 'admin@example.com', roles: [], createdAt: '', updatedAt: '' }
    ]
  } catch (error) {
    console.error('Failed to load users:', error)
  }
}

const loadExportTemplates = async () => {
  try {
    const response = await http.get<ExportTemplate[]>(API_ENDPOINTS.EXPORT.TEMPLATES)
    exportTemplates.value = response
  } catch (error) {
    console.error('Failed to load export templates:', error)
  }
}

const loadExportHistory = async () => {
  try {
    // 这里应该有一个获取导出历史的API
    // const response = await http.get<ExportTask[]>('/export/history')
    // exportHistory.value = response
    
    // 模拟数据
    exportHistory.value = []
  } catch (error) {
    console.error('Failed to load export history:', error)
  }
}

// 事件处理
const handleRangeChange = (range: string) => {
  if (range === 'selected') {
    selectedRecords.value = []
  }
}

const handleSelectAllFields = (checked: boolean) => {
  exportForm.fields = checked ? [...allFields] : []
  isIndeterminate.value = false
}

const handleFieldChange = (fields: string[]) => {
  const checkedCount = fields.length
  selectAllFields.value = checkedCount === allFields.length
  isIndeterminate.value = checkedCount > 0 && checkedCount < allFields.length
}

const removeSelectedRecord = (record: Record) => {
  const index = selectedRecords.value.findIndex(r => r.id === record.id)
  if (index > -1) {
    selectedRecords.value.splice(index, 1)
  }
}

const clearSelectedRecords = () => {
  selectedRecords.value = []
}

// 导出操作
const handlePreview = async () => {
  try {
    const params = buildExportParams()
    params.preview = true
    params.limit = 10
    
    const response = await http.post('/export/preview', params)
    previewData.value = response
    showPreviewDialog.value = true
    
  } catch (error) {
    console.error('Preview failed:', error)
    ElMessage.error('预览失败')
  }
}

const handleExport = async () => {
  if (!exportFormRef.value) return
  
  try {
    await exportFormRef.value.validate()
    
    if (exportForm.range === 'selected' && selectedRecords.value.length === 0) {
      ElMessage.warning('请先选择要导出的记录')
      return
    }
    
    if (exportForm.fields.length === 0) {
      ElMessage.warning('请至少选择一个导出字段')
      return
    }
    
    exporting.value = true
    
    const params = buildExportParams()
    const response = await http.post<ExportTask>(API_ENDPOINTS.EXPORT.RECORDS, params)
    
    ElMessage.success('导出任务已创建，请在导出历史中查看进度')
    
    // 添加到导出历史
    exportHistory.value.unshift(response)
    
    // 开始轮询任务状态
    pollTaskStatus(response.id)
    
  } catch (error: any) {
    console.error('Export failed:', error)
    ElMessage.error(error.message || '导出失败')
  } finally {
    exporting.value = false
  }
}

const buildExportParams = () => {
  const params: any = {
    format: exportForm.format,
    fields: exportForm.fields
  }
  
  if (exportForm.templateId) {
    params.templateId = exportForm.templateId
  }
  
  if (exportForm.range === 'filtered') {
    params.filters = exportForm.filters
  } else if (exportForm.range === 'selected') {
    params.recordIds = selectedRecords.value.map(r => r.id)
  }
  
  return params
}

const pollTaskStatus = (taskId: number) => {
  const poll = async () => {
    try {
      const response = await http.get<ExportTask>(`/export/tasks/${taskId}`)
      
      const taskIndex = exportHistory.value.findIndex(t => t.id === taskId)
      if (taskIndex > -1) {
        exportHistory.value[taskIndex] = response
      }
      
      if (response.status === 'processing') {
        setTimeout(poll, 2000) // 2秒后再次轮询
      } else if (response.status === 'completed') {
        ElMessage.success('导出完成')
      } else if (response.status === 'failed') {
        ElMessage.error('导出失败')
      }
      
    } catch (error) {
      console.error('Poll task status failed:', error)
    }
  }
  
  poll()
}

const handleReset = () => {
  Object.assign(exportForm, {
    format: 'excel',
    range: 'all',
    filters: {
      type: [],
      status: [],
      dateRange: null,
      createdBy: []
    },
    fields: ['title', 'type', 'status', 'creator', 'createdAt'],
    templateId: null
  })
  
  selectedRecords.value = []
  handleFieldChange(exportForm.fields)
}

// 导出历史操作
const downloadExportFile = async (task: ExportTask) => {
  if (!task.fileUrl) return
  
  try {
    const response = await http.get(
      API_ENDPOINTS.EXPORT.DOWNLOAD(task.id),
      { responseType: 'blob' }
    )
    
    // 创建下载链接
    const blob = new Blob([response])
    const url = window.URL.createObjectURL(blob)
    const link = document.createElement('a')
    link.href = url
    link.download = `export_${task.id}_${dayjs(task.createdAt).format('YYYY-MM-DD')}.${task.format}`
    link.click()
    window.URL.revokeObjectURL(url)
    
  } catch (error) {
    console.error('Download failed:', error)
    ElMessage.error('下载失败')
  }
}

const cancelExportTask = async (task: ExportTask) => {
  try {
    await http.post(`/export/tasks/${task.id}/cancel`)
    
    const taskIndex = exportHistory.value.findIndex(t => t.id === task.id)
    if (taskIndex > -1) {
      exportHistory.value[taskIndex].status = 'failed'
      exportHistory.value[taskIndex].error = '用户取消'
    }
    
    ElMessage.success('导出任务已取消')
    
  } catch (error) {
    console.error('Cancel task failed:', error)
    ElMessage.error('取消失败')
  }
}

// 监听字段变化
watch(() => exportForm.fields, handleFieldChange, { immediate: true })

// 生命周期
onMounted(() => {
  loadRecordTypes()
  loadUsers()
  loadExportTemplates()
  loadExportHistory()
})
</script>

<style scoped>
.export-view {
  padding: 0;
}

.filter-section {
  background-color: #f5f7fa;
  padding: 20px;
  border-radius: 4px;
  margin: 16px 0;
}

.selected-section {
  background-color: #f5f7fa;
  padding: 20px;
  border-radius: 4px;
  margin: 16px 0;
}

.selected-records {
  min-height: 100px;
}

.empty-selection {
  text-align: center;
  padding: 20px 0;
}

.selected-count {
  margin-bottom: 12px;
  font-weight: 500;
  color: #409eff;
}

.selected-list {
  margin-bottom: 12px;
}

.selected-list .el-tag {
  margin: 4px 8px 4px 0;
}

.more-count {
  color: #909399;
  font-size: 12px;
}

.field-selection {
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  padding: 16px;
}

.field-selection .el-checkbox {
  display: block;
  margin-bottom: 8px;
}

.field-selection .el-checkbox:first-child {
  border-bottom: 1px solid #ebeef5;
  padding-bottom: 8px;
  margin-bottom: 12px;
}

.template-section {
  display: flex;
  gap: 8px;
  align-items: center;
}

.template-section .el-select {
  flex: 1;
}

.export-history {
  max-height: 600px;
  overflow-y: auto;
}

.empty-history {
  text-align: center;
  padding: 40px 0;
}

.history-item {
  border: 1px solid #ebeef5;
  border-radius: 4px;
  padding: 16px;
  margin-bottom: 12px;
}

.history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.history-format {
  font-weight: 500;
  color: #303133;
}

.history-progress {
  margin: 8px 0;
}

.history-info p {
  margin: 4px 0;
  font-size: 12px;
  color: #909399;
}

.history-error {
  color: #f56c6c !important;
}

.history-actions {
  margin-top: 12px;
  display: flex;
  gap: 8px;
}

.preview-content {
  padding: 20px 0;
}

.preview-info {
  margin-bottom: 16px;
  padding: 12px;
  background-color: #f0f9ff;
  border-radius: 4px;
}

.preview-info p {
  margin: 0;
  color: #409eff;
  font-weight: 500;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .template-section {
    flex-direction: column;
    align-items: stretch;
  }
  
  .history-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .history-actions {
    justify-content: center;
  }
}
</style>