<template>
  <div class="template-manager">
    <!-- 操作栏 -->
    <div class="action-bar">
      <el-button type="primary" @click="showCreateDialog = true">
        <el-icon><Plus /></el-icon>
        新建模板
      </el-button>
    </div>

    <!-- 模板列表 -->
    <el-table :data="templates" stripe>
      <el-table-column prop="name" label="模板名称" min-width="150" />
      
      <el-table-column prop="description" label="描述" min-width="200">
        <template #default="{ row }">
          {{ row.description || '无描述' }}
        </template>
      </el-table-column>
      
      <el-table-column prop="format" label="格式" width="80">
        <template #default="{ row }">
          <el-tag size="small">{{ row.format.toUpperCase() }}</el-tag>
        </template>
      </el-table-column>
      
      <el-table-column prop="createdAt" label="创建时间" width="180">
        <template #default="{ row }">
          {{ formatTime(row.createdAt) }}
        </template>
      </el-table-column>
      
      <el-table-column label="操作" width="150" fixed="right">
        <template #default="{ row }">
          <el-button size="small" @click="handleEdit(row)">
            编辑
          </el-button>
          
          <el-popconfirm
            title="确定要删除这个模板吗？"
            @confirm="handleDelete(row)"
          >
            <template #reference>
              <el-button size="small" type="danger">
                删除
              </el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <!-- 创建/编辑模板对话框 -->
    <el-dialog
      v-model="showCreateDialog"
      :title="editingTemplate ? '编辑模板' : '新建模板'"
      width="600px"
    >
      <el-form
        ref="templateFormRef"
        :model="templateForm"
        :rules="templateRules"
        label-width="100px"
      >
        <el-form-item label="模板名称" prop="name">
          <el-input
            v-model="templateForm.name"
            placeholder="请输入模板名称"
          />
        </el-form-item>
        
        <el-form-item label="描述">
          <el-input
            v-model="templateForm.description"
            type="textarea"
            :rows="3"
            placeholder="请输入模板描述"
          />
        </el-form-item>
        
        <el-form-item label="导出格式" prop="format">
          <el-select v-model="templateForm.format" placeholder="选择格式">
            <el-option label="Excel" value="excel" />
            <el-option label="PDF" value="pdf" />
            <el-option label="CSV" value="csv" />
            <el-option label="JSON" value="json" />
          </el-select>
        </el-form-item>
        
        <el-form-item label="导出字段" prop="fields">
          <el-checkbox-group v-model="templateForm.config.fields">
            <el-checkbox label="id">ID</el-checkbox>
            <el-checkbox label="title">标题</el-checkbox>
            <el-checkbox label="type">类型</el-checkbox>
            <el-checkbox label="status">状态</el-checkbox>
            <el-checkbox label="content">内容</el-checkbox>
            <el-checkbox label="creator">创建者</el-checkbox>
            <el-checkbox label="createdAt">创建时间</el-checkbox>
            <el-checkbox label="updatedAt">更新时间</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        
        <el-form-item label="排序方式">
          <el-select v-model="templateForm.config.sortBy" placeholder="选择排序字段">
            <el-option label="创建时间" value="createdAt" />
            <el-option label="更新时间" value="updatedAt" />
            <el-option label="标题" value="title" />
          </el-select>
          
          <el-select
            v-model="templateForm.config.sortOrder"
            placeholder="排序方向"
            style="margin-left: 8px; width: 100px;"
          >
            <el-option label="升序" value="asc" />
            <el-option label="降序" value="desc" />
          </el-select>
        </el-form-item>
      </el-form>
      
      <template #footer>
        <el-button @click="handleCancel">取消</el-button>
        <el-button
          type="primary"
          :loading="saving"
          @click="handleSave"
        >
          {{ editingTemplate ? '更新' : '创建' }}
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted } from 'vue'
import { ElMessage, type FormInstance, type FormRules } from 'element-plus'
import { http } from '@/utils/request'
import { API_ENDPOINTS } from '@/config/api'
import type { ExportTemplate } from '@/types'
import dayjs from 'dayjs'

// Emits
interface Emits {
  (e: 'template-updated'): void
  (e: 'close'): void
}

const emit = defineEmits<Emits>()

// 响应式数据
const templateFormRef = ref<FormInstance>()
const templates = ref<ExportTemplate[]>([])
const showCreateDialog = ref(false)
const saving = ref(false)
const editingTemplate = ref<ExportTemplate | null>(null)

// 表单数据
const templateForm = reactive({
  name: '',
  description: '',
  format: 'excel',
  config: {
    fields: ['title', 'type', 'status', 'creator', 'createdAt'],
    sortBy: 'createdAt',
    sortOrder: 'desc'
  }
})

// 表单验证规则
const templateRules: FormRules = {
  name: [
    { required: true, message: '请输入模板名称', trigger: 'blur' },
    { min: 1, max: 50, message: '模板名称长度在 1 到 50 个字符', trigger: 'blur' }
  ],
  format: [
    { required: true, message: '请选择导出格式', trigger: 'change' }
  ]
}

// 方法
const formatTime = (time: string) => {
  return dayjs(time).format('YYYY-MM-DD HH:mm')
}

// 加载模板列表
const loadTemplates = async () => {
  try {
    const response = await http.get<ExportTemplate[]>(API_ENDPOINTS.EXPORT.TEMPLATES)
    templates.value = response
  } catch (error) {
    console.error('Failed to load templates:', error)
    ElMessage.error('加载模板列表失败')
  }
}

// 重置表单
const resetForm = () => {
  Object.assign(templateForm, {
    name: '',
    description: '',
    format: 'excel',
    config: {
      fields: ['title', 'type', 'status', 'creator', 'createdAt'],
      sortBy: 'createdAt',
      sortOrder: 'desc'
    }
  })
  
  editingTemplate.value = null
}

// 事件处理
const handleEdit = (template: ExportTemplate) => {
  editingTemplate.value = template
  
  Object.assign(templateForm, {
    name: template.name,
    description: template.description || '',
    format: template.format,
    config: { ...template.config }
  })
  
  showCreateDialog.value = true
}

const handleDelete = async (template: ExportTemplate) => {
  try {
    await http.delete(`${API_ENDPOINTS.EXPORT.TEMPLATES}/${template.id}`)
    ElMessage.success('模板删除成功')
    
    await loadTemplates()
    emit('template-updated')
    
  } catch (error) {
    console.error('Delete template failed:', error)
    ElMessage.error('删除失败')
  }
}

const handleSave = async () => {
  if (!templateFormRef.value) return
  
  try {
    await templateFormRef.value.validate()
    
    if (templateForm.config.fields.length === 0) {
      ElMessage.warning('请至少选择一个导出字段')
      return
    }
    
    saving.value = true
    
    const data = {
      name: templateForm.name,
      description: templateForm.description,
      format: templateForm.format,
      config: templateForm.config
    }
    
    if (editingTemplate.value) {
      await http.put(`${API_ENDPOINTS.EXPORT.TEMPLATES}/${editingTemplate.value.id}`, data)
      ElMessage.success('模板更新成功')
    } else {
      await http.post(API_ENDPOINTS.EXPORT.TEMPLATES, data)
      ElMessage.success('模板创建成功')
    }
    
    showCreateDialog.value = false
    resetForm()
    
    await loadTemplates()
    emit('template-updated')
    
  } catch (error: any) {
    console.error('Save template failed:', error)
    ElMessage.error(error.message || '保存失败')
  } finally {
    saving.value = false
  }
}

const handleCancel = () => {
  showCreateDialog.value = false
  resetForm()
}

// 生命周期
onMounted(() => {
  loadTemplates()
})
</script>

<style scoped>
.template-manager {
  padding: 20px 0;
}

.action-bar {
  margin-bottom: 20px;
}
</style>