<template>
  <div class="record-selector">
    <!-- 搜索栏 -->
    <div class="search-bar">
      <el-input
        v-model="searchKeyword"
        placeholder="搜索记录标题"
        clearable
        @keyup.enter="loadRecords"
      >
        <template #append>
          <el-button @click="loadRecords">
            <el-icon><Search /></el-icon>
          </el-button>
        </template>
      </el-input>
      
      <el-select
        v-model="typeFilter"
        placeholder="记录类型"
        clearable
        @change="loadRecords"
        style="width: 150px; margin-left: 8px;"
      >
        <el-option
          v-for="type in recordTypes"
          :key="type.id"
          :label="type.name"
          :value="type.name"
        />
      </el-select>
    </div>

    <!-- 记录列表 -->
    <div class="record-list">
      <el-table
        ref="tableRef"
        :data="records"
        @selection-change="handleSelectionChange"
        max-height="400"
        stripe
      >
        <el-table-column type="selection" width="55" />
        
        <el-table-column prop="title" label="标题" min-width="200" />
        
        <el-table-column prop="type" label="类型" width="120">
          <template #default="{ row }">
            <el-tag size="small">{{ row.type }}</el-tag>
          </template>
        </el-table-column>
        
        <el-table-column prop="status" label="状态" width="100">
          <template #default="{ row }">
            <el-tag
              :type="getStatusType(row.status)"
              size="small"
            >
              {{ getStatusText(row.status) }}
            </el-tag>
          </template>
        </el-table-column>
        
        <el-table-column prop="createdAt" label="创建时间" width="180">
          <template #default="{ row }">
            {{ formatTime(row.createdAt) }}
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <div class="pagination-wrapper">
      <el-pagination
        v-model:current-page="pagination.page"
        v-model:page-size="pagination.pageSize"
        :page-sizes="[10, 20, 50]"
        :total="pagination.total"
        layout="total, sizes, prev, pager, next"
        @size-change="handleSizeChange"
        @current-change="handlePageChange"
      />
    </div>

    <!-- 操作按钮 -->
    <div class="actions">
      <div class="selected-info">
        已选择 {{ selectedRecords.length }} 条记录
      </div>
      <div class="action-buttons">
        <el-button @click="$emit('cancel')">取消</el-button>
        <el-button type="primary" @click="handleConfirm">
          确定选择
        </el-button>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, watch, nextTick } from 'vue'
import { ElMessage } from 'element-plus'
import { http } from '@/utils/request'
import { API_ENDPOINTS } from '@/config/api'
import type { Record, RecordType, PaginatedResponse } from '@/types'
import dayjs from 'dayjs'

// Props
interface Props {
  modelValue: Record[]
}

// Emits
interface Emits {
  (e: 'update:modelValue', value: Record[]): void
  (e: 'confirm'): void
  (e: 'cancel'): void
}

const props = defineProps<Props>()
const emit = defineEmits<Emits>()

// 响应式数据
const tableRef = ref()
const records = ref<Record[]>([])
const recordTypes = ref<RecordType[]>([])
const selectedRecords = ref<Record[]>([...props.modelValue])
const searchKeyword = ref('')
const typeFilter = ref('')

// 分页
const pagination = reactive({
  page: 1,
  pageSize: 20,
  total: 0
})

// 方法
const formatTime = (time: string) => {
  return dayjs(time).format('YYYY-MM-DD HH:mm')
}

const getStatusType = (status: string) => {
  const statusMap: Record<string, string> = {
    draft: 'info',
    published: 'success',
    archived: 'warning'
  }
  return statusMap[status] || 'info'
}

const getStatusText = (status: string) => {
  const statusMap: Record<string, string> = {
    draft: '草稿',
    published: '已发布',
    archived: '已归档'
  }
  return statusMap[status] || status
}

// 加载记录列表
const loadRecords = async () => {
  try {
    const params: any = {
      page: pagination.page,
      pageSize: pagination.pageSize
    }
    
    if (searchKeyword.value) {
      params.keyword = searchKeyword.value
    }
    
    if (typeFilter.value) {
      params.type = typeFilter.value
    }
    
    const response = await http.get<PaginatedResponse<Record>>(
      API_ENDPOINTS.RECORDS.LIST,
      { params }
    )
    
    records.value = response.items
    pagination.total = response.total
    
    // 恢复选中状态
    nextTick(() => {
      if (tableRef.value) {
        records.value.forEach(record => {
          const isSelected = selectedRecords.value.some(r => r.id === record.id)
          if (isSelected) {
            tableRef.value.toggleRowSelection(record, true)
          }
        })
      }
    })
    
  } catch (error) {
    console.error('Failed to load records:', error)
    ElMessage.error('加载记录列表失败')
  }
}

// 加载记录类型
const loadRecordTypes = async () => {
  try {
    const response = await http.get<RecordType[]>(API_ENDPOINTS.RECORD_TYPES.LIST)
    recordTypes.value = response
  } catch (error) {
    console.error('Failed to load record types:', error)
  }
}

// 事件处理
const handleSelectionChange = (selection: Record[]) => {
  // 更新选中记录，保持跨页选择
  const currentPageIds = records.value.map(r => r.id)
  
  // 移除当前页面的记录
  selectedRecords.value = selectedRecords.value.filter(r => !currentPageIds.includes(r.id))
  
  // 添加新选中的记录
  selectedRecords.value.push(...selection)
}

const handlePageChange = (page: number) => {
  pagination.page = page
  loadRecords()
}

const handleSizeChange = (size: number) => {
  pagination.pageSize = size
  pagination.page = 1
  loadRecords()
}

const handleConfirm = () => {
  emit('update:modelValue', selectedRecords.value)
  emit('confirm')
}

// 监听props变化
watch(() => props.modelValue, (newValue) => {
  selectedRecords.value = [...newValue]
}, { deep: true })

// 生命周期
onMounted(() => {
  loadRecords()
  loadRecordTypes()
})
</script>

<style scoped>
.record-selector {
  padding: 20px 0;
}

.search-bar {
  display: flex;
  margin-bottom: 20px;
}

.record-list {
  margin-bottom: 20px;
}

.pagination-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  border-top: 1px solid #ebeef5;
}

.selected-info {
  color: #409eff;
  font-weight: 500;
}

.action-buttons {
  display: flex;
  gap: 8px;
}
</style>